<template lang="pug">
    .module-view
        collapsible-sidebar.sidebar
        .vertical
            breadcrumbs.breadcrumbs
            h2.title( ref="title" ) {{ pageTitle }}
            .action-view-container( ref="action-view-container" )
                router-view.action-view

</template>
<style lang="sass" scoped>
@import '~@/styles/config'
@import '~@/styles/scrollbar'

.module-view
    min-height: calc(100vh - #{$LENGTH__HEADER_HEIGHT})
    display: flex
.vertical
    display: flex
    flex-direction: column
    align-items: stretch
    width: 100%

    .breadcrumbs, h2
        flex: 0 0 auto
    .action-view-container
        @extend .style-scrollbar
        +style-scrollbar
        min-height: 0px
        flex: 1 1 auto
        overflow-y: scroll
        


.title
    font-size: 24px
    line-height: 34px
    font-weight: 300
    padding: 35px 28px
    color: $COLOR__BLACK
    border-bottom: 1px solid $COLOR__LIGHTACCENT
.sidebar
    flex: 1 0 auto


</style>

<script>
import CollapsibleSidebar from '@/components/module/CollapsibleSidebar';
import Breadcrumbs from '@/components/module/Breadcrumbs';
import db from '@/utils/debounce'

export default {
    components: {
        CollapsibleSidebar,
        Breadcrumbs
    },
    computed: {
        pageTitle() {
            return 'Search eKYC and AML'
        }
    },
    methods: {
        handleResize: db(function() {
            
            const actionView = this.$refs['action-view-container'];
            const titleDimensions = this.$refs['title'].getBoundingClientRect();

            const height = window.innerHeight - titleDimensions.bottom;
            // const styles = window.getComputedStyle(actionView);
            // const height = styles.getPropertyValue('height');
            
            actionView.style.height = `${height}px`;

        }, 500)
    },
    mounted() {
        window.addEventListener('resize', this.handleResize);
        this.handleResize();
    }
}
</script>
