import Vue from 'vue'
import Router from 'vue-router'
import ModuleView from '@/views/ModuleView'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/onboarding/search'
    },
    {
      name: 'onboarding',
      path: '/onboarding',
      component: ModuleView,
      redirect: '/onboarding/search',
      children: [
        {
          name: 'search',
          path: 'search',
          meta: {
            shortTitle: 'Search',
            title: 'Search eKYC and AML'
          },
          component: () => import('@/views/OnboardingSearchView'),
        }   
      ]
    }
  ]
})
router.beforeEach((to,from,next)=>{
  if (to.meta.title)
    document.title = `Frankie: ${to.meta.title}`;
  next();
})
export default router;